/**  */
const ZERO = 0;
/**  */
const ONE = 1;

/**
 *   
 * @public
 * @export
 * @function getRandomNumber
 * @param min  
 * @param max  
 * @return {Number}
 */
export const getRandomNumber = (min = ZERO, max = ONE) => {
  if (typeof min !== 'number' || typeof max !== 'number') {
    throw new Error('getRandomNumber()      ');
  }
  if (max < min) {
    throw new Error('getRandomNumber() max    min');
  }
  return Math.floor(Math.random() * (max - min + ONE)) + min;
};

/**
 *     
 * @public
 * @export
 * @function getRandomArrayElement
 * @param array 
 * @return {Array}
 */
export const getRandomArrayElement = (array) => {
  if (!Array.isArray(array)) {
    throw new Error('getRandomArrayElements()     ');
  }
  return array[getRandomNumber(ZERO, array.length - ONE)];
};
