/**
 *          
 * @public
 * @export
 * @function capitalize
 * @param str 
 * @return {String}
 */
export const capitalize = (str) => {
  if (typeof str !== 'string') {
    throw new Error('capitalize()     ');
  }
  return str.length === 0 ? '' : str.charAt(0).toUpperCase() + str.slice(1).toLowerCase();
};
